<script>
import $ from "jquery";
export default {
	data: () => ({
		model:0,
		imgHeight: '100%',
		worksItem: []
	}),
	computed:{
		works(){
			return this.$store.getters.getWorks;
		},
		workIndex(){
			return this.$store.getters.getWorksSelect;
		}
	},
	created() {
		window.addEventListener('beforeunload', this.handler)
		if(this.workIndex == null){
			this.$router.push('/uiux');
		}
		this.worksItem = this.works[this.workIndex];
		this.model = this.workIndex;
	},
	mounted(){
		this.$store.dispatch('change_title',this.worksItem.title);
	},
	methods:{
		handler(event) {
			//  event
			// event.returnValue = 'Write something'
			alert(this.$refs.message.value)
		},
		handleResize(){
			var width = window.innerWidth;
			// console.log("width",width);
			// if( width < 1600 ){
			// 	this.imgHeight = 650;
			// }if( width < 1200 ){
			// 	this.imgHeight = 600;
			// }if( width < 900 ){
			// 	this.imgHeight = 500;
			// }if( width < 700 ){
			// 	this.imgHeight = 400;
			// }if( width < 600 ){
			// 	this.imgHeight = 200;
			// }else{
			// 	this.imgHeight = `100%`;
			// }
		},
		selectItem(item, index){
			this.worksItem = item;
			this.$store.dispatch('change_title',this.worksItem.title);
		}
	},
	watch:{
	},
	updated() {
		this.$nextTick(function () {
		});
	},
	destroyed() {
		window.removeEventListener('resize', this.handleResize)
	}
}
</script>

