<template>
	<v-container fluid class="pa-0 ma-0">
		<v-row cols="12" align="start" justify="center">
			<v-col cols="12" class="pa-0 ma-0">
				<v-tabs-items v-model="tab">
					<v-tab-item
						v-for="i in tabs"
						:key="i.title"
					>
					<v-img :src="require(`@/assets/portfolio/${i.src}`)" class="mainContents_img">
						<v-col cols="12" align="start" class="main_item" :class="i.color">
							<p class="main_title" color="primary">{{i.title}}</p>
							<p class="main_sub">{{i.sub}}</p>
							<img :src="require(`@/assets/portfolio/${i.icon}`)" class="icon_img">
						</v-col>
					</v-img>
					<v-card flat>
						<v-col 
							cols="12"
							v-for="content in i.contents" 
							:key="content.title" 
							align="center" 
							justify="start" 
							class="pa-0 ma-0"
						>
							<v-col cols="10" align="start"  class="ma-3">
								<v-card-text class="maincontents_title" color="primary">
									{{content.title}}
								</v-card-text>
								<v-card-text class="maincontents_text">
									{{content.text}}
								</v-card-text>
							</v-col>
							<v-img
								class="mainContents_img" 
								:src="require(`@/assets/portfolio/${content.src}`)">
							</v-img>
						</v-col>
					</v-card>
					</v-tab-item>
				</v-tabs-items>
				<v-tabs
					v-model="tab"
					color="primary"
					center
				>
					<v-tab v-for="item in tabs" :key="item.title" v-show="smallWidth">
						{{item.title}}
					</v-tab>
				</v-tabs>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
import $ from "jquery";
export default {
	data: () => ({
		tab: null,
		smallWidth: false
	}),
	computed:{
		tabs(){
			return this.$store.getters.getMain;
		}
	},
	created() {
		if(this.$store.getters.getMainSelect == null){
			this.$router.push('/').catch(err => err);
		}
		this.tab = this.$store.getters.getMainSelect;
		window.addEventListener('resize', this.handleResize)
		this.handleResize();
  },
	mounted(){
		if(this.smallWidth == true){
			this.$eventBus.$emit('drawerHome', false);
		}		
	},
	methods:{
		handleResize(){
			var width = window.innerWidth;
			if( width > 1264 ){
				this.smallWidth = true;
			}if( width < 600 ){
				this.smallWidth = false;
			}
		},
		selectItem(item, index){
			this.worksItem = item;
			this.$store.dispatch('change_title',);
		}
	},
	watch:{
		tab(value){
			this.$store.dispatch('change_title',this.tabs[value].title);
		}
	},
	updated() {
		this.$nextTick(function () {
		});
	},
	destroyed() {
		window.removeEventListener('resize', this.handleResize);
	}
}
</script>

